export default {

	data() {
		return {
			openId: '',
			imgUrl:'',
			avatarUrl:'',
			name:'',
		}
	},
	onLoad(e) {
		console.log(e)
		let id = e.id
		if(id == '1'){
			uni.getUserInfo({
				success:res=>{
					console.log(res)
					this.name = res.userInfo.nickName
					this.avatarUrl = res.userInfo.avatarUrl
					uni.getStorage({
						key:'openId',
						success:res=>{
							this.openId = res.data
							this.requestUrl()
						}
					})
				}
			})
		} else {
			uni.getStorage({
				key:'openId',
				success:res=>{
					this.openId = res.data
					this.onName()
					this.requestUrl()
				}
			})
		}
		
	},
	methods:{
		requestUrl(){
			uni.request({
				url: 'https://www.finetwm.com/xmRepair/common/getQRcode',
				method: 'POST',
				header:{
					"content-Type": "application/x-www-form-urlencoded"
				},
				data: {
					openid: this.openId
				},
				success: res => {
					this.imgUrl = res.data.data.success
				},
				fail: err=>{
					console.log(err)
				}
			})
		},
		onName(){
			uni.request({
				url: 'https://www.finetwm.com/xmRepair/shopInfo/getHeadPortraitAndName',
				method: 'POST',
				header:{
					"content-Type": "application/x-www-form-urlencoded"
				},
				data: {
					openid: this.openId
				},
				success: res=>{
					console.log(res.data.data)
					this.name = res.data.data.name
					this.avatarUrl = res.data.data.head_portrait
				}
			})
		},
		photoSave(){
			uni.saveImageToPhotosAlbum({
				filePath:this.imgUrl,
				success: res=>{
					console.log('save success')
				}
			})
		}
	}
}
